.degree-symbol {
  background-image: url(../../degree.svg);
  width: 12px;
  display: inline-block;
  height: 15px;
  background-size: 271%;
  background-position: 64% 26%;
}

.forecast-card {
  width: 20%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px;
  border: 1px solid #c3baba;
  margin: 10px 2px;
  -webkit-box-shadow: 0px 20px 21px -22px #a8a3a3;
          box-shadow: 0px 20px 21px -22px #a8a3a3;
  cursor: pointer;
  position: relative;
}

.forecast-card-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.forecast-card__active {
  background-color: #f5f5f5;
}

.day {
  font-size: 20px;
  text-transform: uppercase;
  color: #474c7b;
  font-family: "Open Sans", Arial, sans-serif;
}

.weather-icon {
  font-size: 55px;
  margin-top: 30px;
  margin-bottom: 30px;
  color: #4999d4;
}

.temp-high,
.temp-low {
  display: inline-block;
  text-align: center;
  padding-left: 6px;
}

.temp-low {
  opacity: 0.5;
}

.temps {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  text-align: center;
}

@media screen and (max-width: 570px) {
  .forecast-card {
    width: 90%;
    margin: 0 auto;
  }

  .weather-icon {
    font-size: 131px;
    margin-top: 10px;
    margin-bottom: 10px;
    color: #4999d4;
  }

  .day,
  .temps {
    font-size: 25px;
  }

  .degree-symbol {
    height: 100%;
    background-size: 400%;
    background-position: 51% -19%;
  }
}



